.project-card {
  padding: 0;
  border-radius: 8px;
  overflow: hidden;
  color: white;
  background: #232526;
  background: linear-gradient(to right, #414345, #232526);
  transition: all 0.5s ease-in-out;
}

.project-card:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.project-image-container {
  border-radius: 8px 8px 0 0;
  height: 250px;
  overflow: hidden;
}

.project-image {
  width: 100%;
  transition: transform 0.5s ease-in-out;
}

.project-card:hover .project-image {
  transform: scale(1.15);
}

.project-details {
  text-align: left;
  padding: 20px;
}

.project-title {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #fff;
}

.project-description {
  font-size: 1rem;
  color: #ccc;
  line-height: 1.4;
  margin-bottom: 15px;
}

.see-more-button {
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  display: inline-flex;
  align-items: center;
}

.arrow-icon {
  margin-left: 5px;
}
