.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  color: #333;
  padding: 10px 20px;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.navbar a {
  color: #333;
  text-decoration: none;
  margin: 0 10px;
  border-bottom: solid 1px #00000000;
  transition: all 0.5s ease-in-out;
}

.navbar a:hover {
  border-bottom: solid 1px #333;
  cursor: pointer;
}

.left-section {
  display: flex;
  align-items: center;
}

.right-section {
  display: flex;
  align-items: center;
}

.right-section a {
  color: #333;
  text-decoration: none;
  margin-left: 20px;
  border-bottom: solid 1px #00000000;
  transition: all 0.5s ease-in-out;
}

.right-section a:hover {
  border-bottom: solid 1px #333;
  cursor: pointer;
}
