#contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 92vh;
  background-color: #ff9a8b;
  background-image: linear-gradient(
    90deg,
    #ff9a8b 0%,
    #ff6a88 55%,
    #ff99ac 100%
  );
}

.contact-form {
  margin: 0;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  min-width: 90vh;
  background: #232526;
  background: linear-gradient(to right, #414345, #232526);
}

.contact-form .success-message {
  color: #33cc33;
}
.contact-form .error-message {
  color: #ff3333;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #fff;
}

input[type="text"],
input[type="email"],
select,
textarea {
  width: calc(100% - 20px);
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

textarea {
  resize: vertical;
  height: 150px;
}

.submit-button {
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
}
