#projects {
  padding: 50px 0;
  text-align: center;
  background-color: #fee140;
  background-image: linear-gradient(90deg, #fee140 0%, #fa709a 100%);
}

#projects h2 {
  margin-bottom: 40px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 80px;
  margin: 20px 220px;
}

@media (max-width: 1100px) {
  .projects-grid {
    margin: 20px;
  }
}
